import React from 'react';
//import { Link } from 'react-router-dom';
import { connect } from 'react-redux'; //Rx
import { createStructuredSelector } from 'reselect';

//import { auth } from '../../firebase/firebase.utils';
import CartIcon from '../cart-icon/cart-icon.component';
import CartDropdown from '../cart-dropdown/cart-dropdown.component';
import { selectCartHidden } from '../../redux/cart/cart.selectors';
import { selectCurrentUser } from '../../redux/user/user.selectors';
import { signOutStart } from '../../redux/user/user.actions';

//import { ReactComponent as Logo } from '../../assets/crown.svg';
//import './header.styles.scss';

import { HeaderContainer, LogoContainer, MainLogo,  OptionsContainer, OptionLink } from './header.styles';

const Header = ({ currentUser, hidden, signOutStart }) => (
    <HeaderContainer>
        <LogoContainer to='/crwn-clothing'>
            <MainLogo />
        </LogoContainer>
        <OptionsContainer>
            <OptionLink to='/crwn-clothing'>
                HOME
            </OptionLink>
            <OptionLink to='/crwn-clothing/shop'>
                SHOP
            </OptionLink>
            <OptionLink to='/crwn-clothing/contact'>
                CONTACT
            </OptionLink>
            {
                currentUser ?
                (
                    <OptionLink as='div' onClick={ signOutStart }>SIGN OUT</OptionLink>
                )
                :
                (
                    <OptionLink to='/crwn-clothing/signin'> 
                        SIGN IN
                    </OptionLink>
                )
            }
            <CartIcon />
        </OptionsContainer>
        {
            hidden ? null : <CartDropdown />
        }
    </HeaderContainer>
)

//const mapStateToProps = state => ({
    //currentUser: state.user.currentUser
//})

// const mapStateToProps = ({ user: { currentUser }, cart: { hidden } }) => ({
//     currentUser,
//     hidden
// });

// const mapStateToProps = state => ({
//     currentUser: selectCurrentUser(state),
//     hidden: selectCartHidden(state)
// });

const mapStateToProps = createStructuredSelector({
    currentUser: selectCurrentUser,
    hidden: selectCartHidden
});

const mapDispatchToProps = dispatch => ({
    signOutStart: () => dispatch(signOutStart())
});

// export default Header;
export default connect(mapStateToProps, mapDispatchToProps)(Header);