import React from 'react';
import { Redirect, Switch, Route } from 'react-router-dom';
import { connect } from 'react-redux'; //Rx
import { createStructuredSelector } from 'reselect';

import './App.css';

import HomePage from './pages/homepage/homepage.component';
import ShopPage from './pages/shop/shop.component';
import SignInAndSignUpPage from './pages/sign-in-and-sign-up/sign-in-and-sign-up-out.component';
import CheckoutPage from './pages/checkout/checkout.component';
import ContactPage from './pages/contact/contact-component';

import Header from './components/header/header.component';

import { selectCurrentUser } from './redux/user/user.selectors';
import { checkUserSession } from './redux/user/user.actions';

//import { selectCollectionsForPreview } from './redux/shop/shop.selector';

//Let's convert function into class component
class App extends React.Component {

  unsubscribeFromAuth = null;

  componentDidMount() {
    const { checkUserSession } = this.props;
    checkUserSession();
  }

  componentWillUnmount() {
    this.unsubscribeFromAuth();
  }

  render() {
    return (
      <div>
        <Header /*currentUser={this.state.currentUser}*/ />      
        <Switch>
  
          <Route path="crwn-clothing/" exact>
              <Redirect to="crwn-clothing" />
          </Route>
  
          <Route exact path="/crwn-clothing" component={ HomePage } />
          <Route path="/crwn-clothing/shop" component={ ShopPage } />
          <Route exact path='/crwn-clothing/checkout' component={ CheckoutPage } />
          <Route exact path='/crwn-clothing/contact' component={ ContactPage } />

          <Route 
            exact 
            path='/crwn-clothing/signin' 
            render={() => this.props.currentUser ? (
                <Redirect to='/crwn-clothing' />
              ) : (
                <SignInAndSignUpPage />
              ) 
            } 
          />
        </Switch>     
      </div>
    );
  }
}

const mapStateToProps = createStructuredSelector({
  currentUser: selectCurrentUser,
  // collectionsArray: selectCollectionsForPreview
});

const mapDispatchToProps = dispatch => ({
  checkUserSession: () => dispatch(checkUserSession())
});

export default connect(mapStateToProps, mapDispatchToProps)(App);